<template>
  <div class="wrapper">
    <div class="content_box">
      <div class="page_title color333">用户注册</div>
      <div class="page_wrapper">
        <div class="item">
          <div class="item_name">登录邮箱<span>*</span></div>
          <el-input placeholder="请输入登录邮箱" v-model="email" clearable></el-input>
        </div>
        <div class="item">
          <div class="item_name">密码<span>*</span></div>
          <el-input placeholder="请输入密码" v-model="password" show-password></el-input>
        </div>
        <div class="item">
          <div class="item_name">确认密码<span>*</span></div>
          <el-input placeholder="请再次输入密码" v-model="passwordagain" show-password></el-input>
        </div>
        <div class="item">
          <div class="item_name">相关设计经验等级<span>*</span></div>
          <div>
            <el-radio v-model="experience" label="1">无经验</el-radio>
            <el-radio v-model="experience" label="2">初步经验</el-radio>
            <el-radio v-model="experience" label="3">有经验</el-radio>
            <el-radio v-model="experience" label="4">经验丰富</el-radio>
          </div>
        </div>
        <div class="item">
          <div class="item_name">教育层次<span>*</span></div>
          <div>
            <el-radio v-model="education" label="1">本科</el-radio>
            <el-radio v-model="education" label="2">硕士</el-radio>
            <el-radio v-model="education" label="3">博士/1至1.5年</el-radio>
            <el-radio v-model="education" label="4">博士/2.5年以上</el-radio>
          </div>
        </div>
        <div class="page_btnbox"><el-button class="btn" type="primary" @click="registerMed">确定</el-button></div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      email: '',
      password: '',
      passwordagain: '', // 确认密码
      checkList: ['选中且禁用', '复选框 A'],
      experience: '', // 经验
      education: '' // 教育层次
    }
  },
  methods: {
    /**
     * 用户注册
     */
    registerMed: function () {
      // 判断登录邮箱格式是否正确
      if (!this.email) {
        this.$alert('请输入登录邮箱', '页面提示', {
          confirmButtonText: '确定',
          callback: action => {}
        })
        return ''
      } else {
        var reg = new RegExp('^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$')
        if (!reg.test(this.email)) {
          this.$alert('邮箱输入有误', '页面提示', {
            confirmButtonText: '确定'
          })
          return ''
        }
      }
      // 校验输入的密码
      if (!this.password) {
        this.$alert('请输入密码', '页面提示', {
          confirmButtonText: '确定'
        })
        return ''
      }
      if (!this.passwordagain) {
        this.$alert('请输入确认密码', '页面提示', {
          confirmButtonText: '确定'
        })
        return ''
      }
      if (this.password !== this.passwordagain) {
        this.$alert('两次密码输入不一致', '页面提示', {
          confirmButtonText: '确定'
        })
        return ''
      }
      // 经验
      if (!this.experience) {
        this.$alert('请选择相关设计经验等级', '页面提示', {
          confirmButtonText: '确定'
        })
        return ''
      }
      // 学历
      if (!this.education) {
        this.$alert('请选择教育层次', '页面提示', {
          confirmButtonText: '确定'
        })
        return ''
      }
      // 跳转到首页
      this.$router.push({
        path: '/login'
      })
    }
  }
}
</script>
<style lang="less" scoped>
.wrapper {
  width: 100%;
  height: auto;
}
.content_box {
  width: 800px;
  min-height: 500px;
  margin: 34px auto;
  background: #fff;
  overflow: hidden;
  .page_title {
    text-align: center;
    font-size: 24px;
    line-height: 1.8;
    margin: 25px auto 50px;
    position: relative;
    cursor: default;
  }
  .page_title::after {
    content: "";
    display: block;
    position: absolute;
    height: 4px;
    width: 20px;
    background: sandybrown;
    left: 50%;
    margin-left: -10px;
    bottom: -5px;
  }
  .page_wrapper {
    width: 400px;
    margin: 0 auto;
    .item {
      margin-bottom: 10px;
      .item_name {
        line-height: 2;
        font-size: 14px;
        span {
          color: red;
        }
      }
      .not_userbox {
        font-size: 14px;
        color: #999;
        span {
          color: sandybrown;
          cursor: pointer;
          text-decoration: underline;
        }
      }
    }
    .page_btnbox {
      margin: 30px auto;
      text-align: center;
      .btn {
        width: 140px;
      }
    }
  }
}
</style>



