<template>
  <div class="wrapper global_flex wrapper_style">
    <div class="website_name">CAPIDPPK</div>
    <!-- 菜单 -->
    <div class="global_flex menu_box">
      <div class="global_flex">
        <ul class="menulist global_flex" @click="handleMenuClick">
          <li data-route="/">首页</li>
          <li data-route="/evaluation">能力评价</li>
          <li data-route="/crawl">专利采集</li>
          <li data-route="/recommend">专利推荐</li>
          <li data-route="/design">创新设计</li>
        </ul>
        <div class="search_box global_flex">
          <input type="text" placeholder="请输入关键字" />
          <button class="color333">查找</button>
        </div>
      </div>
      <!-- 个人中心 退出 -->
      <div class="user_box global_flex">
        <div class="user_login" v-if="!isLogin" @click="toLogin">登录/注册</div>
        <div class="user_center" v-if="isLogin">个人中心</div>
        <div v-if="isLogin" @click="logoutMed">注销</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      isLogin: false // 用户是否登录
    }
  },
  mounted () {
    // 获取用户是否登录过
    var userinfo = localStorage.getItem('userinfo')
    if (userinfo) {
      this.isLogin = true
    } else {
      this.isLogin = false
    }
  },
  methods: {
    /**
     * 登录注册
     */
    toLogin: function () {
      console.log('login....')
      this.$router.push({
        path: '/login'
      })
    },
    /**
     * 菜单点击
     */
    handleMenuClick: function (ev) {
      if (!ev.target.dataset.route || (ev.target.dataset.route === this.$route.path)) {
        return
      }
      this.$router.push({
        path: ev.target.dataset.route
      })
    },
    /**
     * 用户注销
     */
    logoutMed: function () {
      this.$confirm('您确定退出登录吗?', '页面提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.isLogin = false
        localStorage.removeItem('userinfo')
        if (this.$route.path === '/') {
          window.location.reload()
          return
        }
        this.$router.push({
          path: '/'
        })
        window.location.reload()
      }).catch(() => {})
    }
  }
}
</script>
<style lang="less" scoped>
.wrapper_style {
  justify-content: space-between;
  align-items: center;
}
.wrapper {
  width: 1000px;
  height: 100%;
  margin: 0 auto;
  .website_name {
    width: 120px;
    cursor: default;
    user-select: none;
  }
  .menu_box {
    width: 880px;
    justify-content: space-between;
    align-items: center;
    .menulist {
      align-items: center;
      li {
        margin-right: 15px;
        cursor: pointer;
        user-select: none;
      }
      li:hover {
        color: sandybrown;
      }
    }
  }
  .search_box {
    align-items: center;
    input {
      border: none;
      padding: 0 5px;
      height: 28px;
      line-height: 28px;
      border: 1px solid gray;
      border-radius: 4px;
      margin: 0 15px 0 10px;
    }
    button {
      height: 28px;
      line-height: 28px;
      padding: 0 5px;
      cursor: pointer;
    }
  }
  .user_box {
    div {
      cursor: pointer;
      user-select: none;
    }
    div:hover {
      color: sandybrown;
    }
    .user_center, .user_login {
      margin-right: 15px;
    }
  }
}
</style>



