<template>
  <div class="wrapper">
    <div class="page_title color333">创新设计</div>
    <div class="page_content">
      <div class="page_step">
        <div @click="nextStep(0)" class="step_item" :class="(stepNum === 0)?'current':''">第一阶段</div>
        <div @click="nextStep(1)" class="step_item" :class="(stepNum === 1)?'current':''">能力更新</div>
        <div @click="nextStep(2)" class="step_item" :class="(stepNum === 2)?'current':''">第二阶段</div>
      </div>
      <!-- 第一阶段 -->
      <FristDesign v-if="stepNum === 0"></FristDesign>
      <!-- 能力更新 -->
      <AbilityUpdate v-if="stepNum === 1"></AbilityUpdate>
      <!-- 第二阶段 -->
      <ThirdDesign v-if="stepNum === 2"></ThirdDesign>
      <!-- button list -->
      <div class="design_btn">
        <div>
          <el-button-group>
            <el-button type="primary" icon="el-icon-arrow-left">上一篇专利</el-button>
            <el-button type="primary">
              下一篇专利
              <i class="el-icon-arrow-right el-icon--right"></i>
            </el-button>
          </el-button-group>
        </div>
        <div>
          <el-button @click="nextStep" type="primary">{{(stepNum === 2)?"完成":"下一步"}}</el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import FristDesign from '@/components/firstdesign.vue'
import AbilityUpdate from '@/components/abilityupdate.vue'
import ThirdDesign from '@/components/thirddesign.vue'
export default {
  data () {
    return {
      stepNum: 0 // 阶段
    }
  },
  components: {
    FristDesign,
    AbilityUpdate,
    ThirdDesign
  },
  methods: {
    /**
     * 下一步
     */
    nextStep: function (index) {
      if (this.stepNum > 2) {
        return
      }
      if (typeof index === 'number') {
        this.stepNum = index
      } else {
        this.stepNum = this.stepNum + 1
      }
    }
  }
}
</script>
<style lang="less" scoped>
.wrapper {
  width: 800px;
  padding: 20px 30px;
  background: #fff;
  margin: 34px auto;
  padding-bottom: 60px;
  .page_title {
    text-align: center;
    font-size: 24px;
    line-height: 1.8;
    margin: 25px auto 50px;
    position: relative;
    cursor: default;
  }
  .page_title::after {
    content: "";
    display: block;
    position: absolute;
    height: 4px;
    width: 20px;
    background: sandybrown;
    left: 50%;
    margin-left: -10px;
    bottom: -5px;
  }
  .page_content {
    width: 700px;
    height: auto;
    margin: 0 auto;
    .page_step {
      display: flex;
      .step_item {
        margin-right: 20px;
        cursor: pointer;
        font-size: 14px;
        position: relative;
      }
      .step_item:hover {
        color: sandybrown;
      }
      .current {
        color: sandybrown;
      }
    }
  }
  .design_btn {
    display: flex;
    justify-content: space-between;
    margin: 20px 0;
  }
}
</style>



