import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/index'
import Evaluation from '@/views/evaluation'
import Crawl from '@/views/crawl'
import Recommend from '@/views/recommend'
import Design from '@/views/design'
import Login from '@/views/login'
import Register from '@/views/register'
import Patentlist from '@/views/patentlist'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/evaluation',
      name: 'Evaluation',
      component: Evaluation
    },
    {
      path: '/crawl',
      name: 'Crawl',
      component: Crawl
    },
    {
      path: '/recommend',
      name: 'Recommend',
      component: Recommend
    },
    {
      path: '/design',
      name: 'Design',
      component: Design
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/patentlist',
      name: 'Patentlist',
      component: Patentlist
    }
  ],
  scrollBehavior () {
    return {
      x: 0,
      y: 0
    }
  }
})



