<template>
  <div class="wrapper">
    <div class="page_title color333">专利采集展示</div>
    <div class="page_content">
      <div class="item">CN206777198U 一种鲜橙去皮装置</div>
      <div class="item">CN206006842U 一种榨汁机</div>
      <div class="item">CN204764923U 榨汁机传送装置</div>
      <div class="item">CN201710459475 一种去皮刀具</div>
      <div class="item">CN206006842U 一种榨汁机</div>
      <div class="item">CN201821848745 用于水果加工的果皮去除装置</div>
      <div class="item">CN201410565158 鲜榨橙汁自动售货机的橙子储存保鲜装置</div>
    </div>
    <div class="page_number">
      <el-pagination background layout="prev, pager, next" :total="1000"></el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      value1: null,
      value2: null
    }
  }
}
</script>
<style lang="less" scoped>
.wrapper {
  width: 800px;
  padding: 20px 30px;
  background: #fff;
  margin: 34px auto;
  padding-bottom: 60px;
  .page_title {
    text-align: center;
    font-size: 24px;
    line-height: 1.8;
    margin: 25px auto 50px;
    position: relative;
    cursor: default;
  }
  .page_title::after {
    content: "";
    display: block;
    position: absolute;
    height: 4px;
    width: 20px;
    background: sandybrown;
    left: 50%;
    margin-left: -10px;
    bottom: -5px;
  }
  .page_content {
    width: 500px;
    height: auto;
    margin: 0 auto;
    border: 1px solid #ddd;
    background-color: #f8f9fb;
    .item {
      width: 500px;
      height: 34px;
      line-height: 34px;
      border-bottom: 1px dashed #ddd;
      margin: 0 auto 5px;
      padding: 0 5px;
      box-sizing: border-box;
      font-size: 13px;
    }
    .item:last-child {
      margin-bottom: 0;
      border-bottom: none;
    }
  }
  .page_number {
    text-align: center;
    margin-top: 35px;
  }
  .page_btnbox {
    margin: 30px auto 60px;
    text-align: center;
    .btn {
      width: 140px;
    }
  }
}
</style>



