<template>
  <div class="wrapper">
    <div class="page_title color333">能力评价</div>
    <!-- 评价等级 -->
    <div class="rate_box global_flex">
      <div class="rate_title">评价等级：</div>
      <div>1-2星：一般</div>
      <div>3星：正常</div>
      <div>4-5星：优秀</div>
    </div>
    <div class="page_content">
      <div class="item">
        <div class="name">设计计划能力</div>
        <el-rate v-model="value1"></el-rate>
      </div>
      <div class="item">
        <div class="name">设计效率</div>
        <el-rate v-model="value2"></el-rate>
      </div>
      <div class="item">
        <div class="name">设计控制能力</div>
        <el-rate v-model="value3"></el-rate>
      </div>
      <div class="item">
        <div class="name">知识迁移能力</div>
        <el-rate v-model="value4"></el-rate>
      </div>
      <div class="item">
        <div class="name">设计创新能力</div>
        <el-rate v-model="value5"></el-rate>
      </div>
      <div class="item">
        <div class="name">概念生成能力</div>
        <el-rate v-model="value6"></el-rate>
      </div>
      <div class="item">
        <div class="name">能力提升速度</div>
        <el-rate v-model="value7"></el-rate>
      </div>
      <div class="item">
        <div class="name">知识内化能力</div>
        <el-rate v-model="value8"></el-rate>
      </div>
      <div class="item">
        <div class="name">知识整合能力</div>
        <el-rate v-model="value9"></el-rate>
      </div>
    </div>
    <div class="page_btnbox"><el-button class="btn" type="primary">添加评价设计师</el-button></div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      value1: null,
      value2: null,
      value3: null,
      value4: null,
      value5: null,
      value6: null,
      value7: null,
      value8: null,
      value9: null
    }
  }
}
</script>
<style lang="less" scoped>
.wrapper {
  width: 800px;
  padding: 20px 30px;
  background: #fff;
  margin: 34px auto;
  .page_title {
    text-align: center;
    font-size: 24px;
    line-height: 1.8;
    margin: 25px auto 50px;
    position: relative;
    cursor: default;
  }
  .page_title::after {
    content: "";
    display: block;
    position: absolute;
    height: 4px;
    width: 20px;
    background: sandybrown;
    left: 50%;
    margin-left: -10px;
    bottom: -5px;
  }
  .rate_box {
    justify-content: center;
    font-size: 14px;
    color: #999;
    .rate_title {
      color: sandybrown;
    }
    div {
      margin: 0 10px;
    }
  }
  .page_content {
    width: 100%;
    margin-top: 15px;
    .item {
      display: flex;
      justify-content: center;
      height: 34px;
      .name {
        width: 150px;
        font-size: 14px;
      }
    }
  }
  .page_btnbox {
    margin: 30px auto 60px;
    text-align: center;
    .btn {
      width: 140px;
    }
  }
}
</style>



