<template>
  <div class="wrapper">
    <div class="content_box">
      <div class="login_title color333">用户登录</div>
      <div class="login_wrapper">
        <div class="item">
          <div class="item_name">登录邮箱</div>
          <el-input placeholder="请输入登录邮箱" v-model="email" clearable></el-input>
        </div>
        <div class="item">
          <div class="item_name">密码</div>
          <el-input placeholder="请输入密码" v-model="password" show-password></el-input>
        </div>
        <div class="item">
          <div class="not_userbox">没有账号？<span @click="linkRegister">前往注册</span></div>
        </div>
        <div class="login_btnbox"><el-button class="btn" type="primary" @click="loginMed">登录</el-button></div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      email: '',
      password: ''
    }
  },
  methods: {
    /**
     * 前往注册
     */
    linkRegister: function () {
      this.$router.push({
        path: '/register'
      })
    },
    /**
     * 用户登录
     */
    loginMed: function () {
      // 判断登录邮箱格式是否正确
      if (!this.email) {
        this.$alert('请输入登录邮箱', '页面提示', {
          confirmButtonText: '确定',
          callback: action => {}
        })
        return ''
      } else {
        var reg = new RegExp('^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$')
        if (!reg.test(this.email)) {
          this.$alert('邮箱输入有误', '页面提示', {
            confirmButtonText: '确定'
          })
          return ''
        }
      }
      // 校验输入的密码
      if (!this.password) {
        this.$alert('请输入登录密码', '页面提示', {
          confirmButtonText: '确定'
        })
        return ''
      }
      var obj = {
        email: this.email,
        password: this.password
      }
      localStorage.setItem('userinfo', JSON.stringify(obj))
      // 跳转到首页
      this.$router.push({
        path: '/'
      })
      window.location.reload()
    }
  }
}
</script>
<style lang="less" scoped>
.wrapper {
  width: 100%;
  height: auto;
}
.content_box {
  width: 800px;
  min-height: 500px;
  margin: 34px auto;
  background: #fff;
  overflow: hidden;
  .login_title {
    text-align: center;
    font-size: 24px;
    line-height: 1.8;
    margin: 25px auto 50px;
    position: relative;
    cursor: default;
  }
  .login_title::after {
    content: "";
    display: block;
    position: absolute;
    height: 4px;
    width: 20px;
    background: sandybrown;
    left: 50%;
    margin-left: -10px;
    bottom: -5px;
  }
  .login_wrapper {
    width: 400px;
    margin: 0 auto;
    .item {
      margin-bottom: 10px;
      .item_name {
        line-height: 2;
        font-size: 14px;
      }
      .not_userbox {
        font-size: 14px;
        color: #999;
        span {
          color: sandybrown;
          cursor: pointer;
          text-decoration: underline;
        }
      }
    }
    .login_btnbox {
      margin: 30px auto;
      text-align: center;
      .btn {
        width: 140px;
      }
    }
  }
}
</style>



