<template>
  <div id="app">
    <div class="header_box">
      <Header></Header>
    </div>
    <div class="app_wrapper">
      <router-view />
    </div>
  </div>
</template>

<script>
import Header from '@/components/header.vue'
export default {
  name: 'App',
  components: {
    Header
  }
}
</script>

<style lang="less">
html, body {
  height: 100%;
}
#app {
  width: 100%;
  min-height: 100%;
  background: #f8f9fb;
  box-sizing: border-box;
  padding-bottom: 60px;
  .header_box {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #1867c0;
    height: 64px;
    color: #fff;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
      0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  }
  .app_wrapper {
    width: 1000px;
    margin: 0 auto;
    padding-top: 64px;
  }
}
</style>



