<template>
  <div class="wrapper">
    <div class="page_title color333">专利采集</div>
    <div class="page_content">
      <div class="item">
        <div class="name">设计需求关键字：</div>
        <el-input v-model="value1" placeholder="请输入关键字"></el-input>
      </div>
      <div class="item">
        <div class="name">计划完成创新设计第一阶段时间T1(天)：</div>
        <el-input v-model="value2" placeholder="请输入时间"></el-input>
      </div>
    </div>
    <div class="page_btnbox">
      <el-button class="btn" type="primary">开始采集</el-button>
      <el-button class="btn" type="primary" @click="linkPatent">查看专利</el-button>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      value1: null,
      value2: null
    }
  },
  methods: {
    /**
     * 跳转到专利列表
     */
    linkPatent: function () {
      this.$router.push({
        path: '/patentlist'
      })
    }
  }
}
</script>
<style lang="less" scoped>
.wrapper {
  width: 800px;
  padding: 20px 30px;
  background: #fff;
  margin: 34px auto;
  .page_title {
    text-align: center;
    font-size: 24px;
    line-height: 1.8;
    margin: 25px auto 50px;
    position: relative;
    cursor: default;
  }
  .page_title::after {
    content: "";
    display: block;
    position: absolute;
    height: 4px;
    width: 20px;
    background: sandybrown;
    left: 50%;
    margin-left: -10px;
    bottom: -5px;
  }
  .page_content {
    width: 100%;
    height: auto;
    padding-bottom: 30px;
    .item {
      width: 500px;
      margin: 0 auto 20px;
      .name {
        font-size: 14px;
        color: #333;
        margin-bottom: 10px;
      }
    }
  }
  .page_btnbox {
    margin: 30px auto 60px;
    text-align: center;
    .btn {
      width: 140px;
    }
  }
}
</style>



