import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  isLogin: false
}

const mutations = {
  updateUserState (state, status) {
    state.isLogin = status
  },
  add (state) {
    state.count += 1
  },
  reduce (state) {
    state.count -= 1
  }
}
export default new Vuex.Store({
  state,
  mutations
})



